.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.Link {
  height: 30px;
  margin: 10px;
  color: #3D5B59;
}

body {
  background: linear-gradient(-45deg, #B99095, #FCB5AC, #B5E5CF, #3D5B59);
  background-size: 400% 400%;
  animation: gradient 15s ease infinite;
  height: 100vh;
}

@keyframes gradient {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}
